import React from 'react';
import { graphql } from 'gatsby';
import Helmet from 'react-helmet';
import IndexLayout from '../../layouts';
import { Page } from '../../components/layout/Page';
import styled from 'styled-components';
import { SiteMetadata } from '../../interfaces/gatsby';

import { DocsWrapper } from '../../components/docs/DocsWrapper';
import { Container } from '../../components/layout/Container';
import { PALDocsHeader } from '../../components/docs/DocsHeader';
import { MarkdownContent } from '../../components/page/Markdown';
import { FooterWrapper, PALFooter } from 'components/layout/Footer';
import { BackToTopButton } from 'components/docs/BackToTopButton';
import SEO from '../seo';
import {
  HeaderPara,
  Para,
  Card,
  CardHeader,
  CardImage,
  CardTechImage,
  CardContent,
  CardSection,
  FluidCard,
  FluidImage
} from '../../components/styles';

import { Heading, Paragraph } from 'components/foundations';

import BIAsAService from 'assets/images/svg/BI-as-a-service.svg';
import BIConsulting from 'assets/images/svg/BIConsulting.svg';
import BIImplementation from 'assets/images/svg/BIImplementation.svg';
import BISupport from 'assets/images/svg/BISupport.svg';
// import innovation from 'assets/images/svg/innovation.svg';

import Tensorflow from 'assets/images/technologies/tensorflow-ar21.svg';
import Python from 'assets/images/technologies/python-ar21.svg';

import PowerBI from 'assets/images/technologies/microsoft_powerbi-ar21.svg';
import Pytorch from 'assets/images/technologies/pytorch-ar21.svg';
import SQLite from 'assets/images/technologies/sqlite-ar21.svg';
import RProject from 'assets/images/technologies/r-project-ar21.svg';
import Jupyter from 'assets/images/technologies/jupyter-ar21.svg';
import Pandas from 'assets/images/technologies/usepanda-ar21.svg';
import NodeJS from 'assets/images/technologies/nodejs-ar21.svg';

interface Props {
  data: {
    site: {
      siteMetadata: SiteMetadata;
    };
  };
}

const Container1 = styled(Container)`
  background: #f9f9fc;
`;

const Content = styled('div')`
  width: 100%;
  padding: 20px;
`;

const BusinessIntelligencePage: React.FC<Props> = ({ data }) => {
  const { site } = data;
  const { siteMetadata } = site;
  const title = 'Business Intelligence';
  const description =
    'We are offering leading reporting solutions using different tools of BI. Our extensive and proven experience in forecasting, strategy, optimization, performance analysis, trend analysis, customer analysis, budget planning, financial reporting and more.';
  // const url = 'https://www.palportals.com/services/business_intelligence';
  // https://css-tricks.com/fluid-images-in-a-variable-proportion-layout/
  return (
    <IndexLayout navHidden>
      <Page docsPage>
        <SEO title={title} description={description} />
        {/* <Helmet>
          <title>
            {title} &middot; {data.site.siteMetadata.title}
          </title>
          <meta property="og:title" content={title} />
          <meta name="description" content={description} />
          <meta property="og:description" content={description} />
          <meta property="og:locale" content="en_US" />
          <meta property="og:type" content="website" />
          <meta property="og:site_name" content={data.site.siteMetadata.title} />
          <meta name="twitter:card" content={title} />
          <meta name="twitter:description" content={description} />
          <meta name="twitter:title" content={title} />
          <meta property="og:url" content={url} />
        </Helmet> */}
        <DocsWrapper>
          <Container>
            <PALDocsHeader
              title="Business Intelligence"
              subtitle="We are offering leading reporting solutions using different tools of BI. Our extensive and proven
              experience in forecasting, strategy, optimization, performance analysis, trend analysis, customer
              analysis, budget planning, financial reporting and more."
            />
            <MarkdownContent style={{ boxSizing: 'border-box' }}>
              <Para>
                Business intelligence (BI) leverages software and services to transform data into actionable insights
                that inform an organizationâ€™s strategic and tactical business decisions. BI tools access and analyze
                data sets and present analytical findings in reports, summaries, dashboards, graphs, charts and maps to
                provide users with detailed intelligence about the state of the business.
              </Para>
              <HeaderPara style={{ marginTop: '100px', marginBottom: '100px' }}>
                We offers following Business Intelligence services:
              </HeaderPara>
              <FluidCard>
                <FluidImage src={BIAsAService} alt="Partnership" />
                <Content>
                  <Heading>Data analytics</Heading>
                  <Paragraph>
                    We offers Business Analysis portfolios tools having ability to translate data into actionable
                    information, so that organizations can make decisions that will enhance profitability and
                    productivity.
                  </Paragraph>
                  <ul>
                    <li>Analytical Dashboard</li>
                    <li>Drawing inferences</li>
                    <li>Quantitative analysis</li>
                    <li>Classifying analysis</li>
                    <li>Classifying analysis</li>
                    <li>Survey design</li>
                    <li>Formulating data queries</li>
                  </ul>
                </Content>
              </FluidCard>
              <FluidCard>
                <FluidImage src={BIAsAService} alt="BI as a service" />
                <Content>
                  <Heading>BI as a service</Heading>
                  <Paragraph>
                    We offers a range of enterprise-level business intelligence and analytics solutions that help you
                    see you beyond the data.
                  </Paragraph>
                  <ul>
                    <li>BI Analytics</li>
                    <li>Data Warehousing</li>
                    <li>Data Modeling</li>
                    <li>Custom Visualization</li>
                    <li>Predictive Analytics</li>
                    <li>Cloud BI</li>
                    <li>Cloud BI Migration</li>
                  </ul>
                </Content>
              </FluidCard>
              <FluidCard>
                <FluidImage src={BIConsulting} alt="BI consulting" />
                <Content>
                  <Heading>BI consulting</Heading>
                  <Paragraph>
                    We offers BI consulting services that help companies retrieve actionable insights out of their raw
                    data to accelerate decision-making and improve business performance.
                  </Paragraph>
                  <ul>
                    <li>BI needs analysis</li>
                    <li>BI implementation/evolution/migration strategy</li>
                    <li>BI solution design</li>
                    <li>BI solution cloud migration supervision</li>
                    <li>Advising on data quality and data security</li>
                    <li>User adoption strategies</li>
                    <li>Proof of concept (for complex projects)</li>
                  </ul>
                </Content>
              </FluidCard>
              <FluidCard>
                <FluidImage src={BIImplementation} alt="BI implementation" />
                <Content>
                  <Heading>BI implementation</Heading>
                  <Paragraph>
                    We offers BI implementation services that involves designing, developing, deploying a BI solution to
                    integrate, process, and analyze historical and real-time data. The aim of BI implementation is to
                    support decision-making at strategic, tactical and operational levels.
                  </Paragraph>
                  <ul>
                    <li>Development of the BI solution components</li>
                    <li>BI flow process</li>
                    <li>BI reports and dashboards design</li>
                    <li>BI data management process</li>
                    <li>BI data quality assurance process</li>
                  </ul>
                </Content>
              </FluidCard>
              <FluidCard>
                <FluidImage src={BISupport} alt="BI support" />
                <Content>
                  <Heading>BI support</Heading>
                  <Paragraph>
                    We offers BI support services that help companies ensure the sound performance of their BI solutions
                    and leverage their potential to the fullest.
                  </Paragraph>
                  <ul>
                    <li>Development support</li>
                    <li>Troubleshooting on the code level </li>
                    <li>Administration support</li>
                  </ul>
                </Content>
              </FluidCard>
              <FluidCard>
                <FluidImage src={BIImplementation} alt="BI implementation" />
                <Content>
                  <Heading>BI implementation</Heading>
                  <Paragraph>
                    We offers BI implementation services that involves designing, developing, deploying a BI solution to
                    integrate, process, and analyze historical and real-time data. The aim of BI implementation is to
                    support decision-making at strategic, tactical and operational levels.
                  </Paragraph>
                  <ul>
                    <li>Development of the BI solution components</li>
                    <li>BI flow process</li>
                    <li>BI reports and dashboards design</li>
                    <li>BI data management process</li>
                    <li>BI data quality assurance process</li>
                  </ul>
                </Content>
              </FluidCard>
            </MarkdownContent>
            <MarkdownContent>
              <Container1>
                <HeaderPara style={{ marginBottom: '50px', paddingTop: '50px' }}>Technologies expertise:</HeaderPara>
                <CardSection>
                  <CardTechImage>
                    <img src={Tensorflow} alt="Tensor Flow" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={Python} alt="Python" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={PowerBI} alt="Power BI" />
                  </CardTechImage>
                </CardSection>
                <CardSection>
                  <CardTechImage>
                    <img src={Pytorch} alt="Pytorch" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={SQLite} alt="SQLite" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={RProject} alt="R-Project" />
                  </CardTechImage>
                </CardSection>
                <CardSection>
                  <CardTechImage>
                    <img src={Jupyter} alt="Jupyter" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={Pandas} alt="Pandas" />
                  </CardTechImage>
                  <CardTechImage>
                    <img src={NodeJS} alt="NodeJS" />
                  </CardTechImage>
                </CardSection>
              </Container1>
            </MarkdownContent>

            <HeaderPara style={{ marginTop: '150px', marginBottom: '100px', display: 'none' }}>Case Studies</HeaderPara>
            <HeaderPara style={{ marginTop: '50px', marginBottom: '50px', display: 'none' }}>
              Recent Articles
            </HeaderPara>
            <FooterWrapper>
              <PALFooter
                version={siteMetadata.version}
                siteLastUpdated={siteMetadata.siteLastUpdated}
                socials={siteMetadata.socials}
              />
            </FooterWrapper>
          </Container>
          <BackToTopButton href="#" />
        </DocsWrapper>
      </Page>
    </IndexLayout>
  );
};

export default BusinessIntelligencePage;

export const query = graphql`
  query BusinessIntelligencePageQuery {
    site {
      siteMetadata {
        title
        sidebarTitle
        sidebarSubtext
        siteLastUpdated
        description
        version
        siteUrl
        keywords
        author {
          name
          email
        }
        socials {
          name
          imgpath
          url
        }
      }
    }
  }
`;
